import React, {useState} from 'react'
import QRCode from "react-qr-code";
import {
    Box,
    Button,
    Card,
    CardContent,
    Dialog, DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Typography
} from "@mui/material";
import {Link, useHistory} from "react-router-dom";

export default function AccountSettings(props) {

    const [open, setOpen] = useState(false)
    let history = useHistory()

    const userID = localStorage.getItem('userID')

    const handleClose = keep => {
        props.resetApp(keep)
        history.push('/')
    }

    const style = {
        card: {
            margin: '16px',
        },
        box: {
            margin: '0 16px 0 16px',
            display: 'flex',
            justifyContent: 'flex-end'
        }
    }

    return (
        <React.Fragment>
            <Card sx={style.card}>
                <CardContent>
                    <Typography variant='subtitle1' sx={{marginBottom: '32px'}}>Az azonosítód: {userID}</Typography>
                    <QRCode value={window.location.hostname + process.env.PUBLIC_URL + '#/linkAccount?userID=' + userID}/>
                </CardContent>
            </Card>
            <Box sx={style.box}>
                <Link to={'/'}><Button variant='contained' sx={{marginRight: '16px'}}>Vissza</Button></Link>
                <Button variant='contained' onClick={() => setOpen(true)}
                              color='error'>Fiók törlése</Button>
            </Box>
            <Dialog
                open={open}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                onClose={() => {setOpen(false)}}
            >
                <DialogTitle id="alert-dialog-title">
                    {"Fiók adatainak törlése"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Egy új fiók létrehozásával alapértelmezetten törlődnek az ahhoz kapcsolt adatok. Lehetősége van megtartani az adatokat, ha késöbb mégis szeretné újból használni a fiókot.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={()=>{handleClose(true)}}>Megtartás</Button>
                    <Button color={'error'} onClick={()=>{handleClose(false)}} autoFocus>
                        Törlés
                    </Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}